import message_item from "@/components/user/notify/message_item";

export default {
  name: "notify",
  components: {
    message_item
  },
  data() {
    return {
      page_num: undefined,
      page: 1,
      message_list: [],
      total_count: undefined
    }
  },
  created() {
    this.load_message_list();
  },
  methods: {
    on_page_change:function (e){

    },
    load_message_list: function () {
      this.$axios.get('/app/message/list', {
        params: {
          id: this.id,
        }
      }).then((response) => {
        console.log("response:", response);
        this.message_list = response.data.messages;
        this.total_count = response.data.total_count;
      }).catch((error) => {
      });
    }
  }
}
